@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@300;400;500;600;700;800&display=swap);
.header{height:8rem;position:fixed;top:0;left:0;width:100%;z-index:99;transition:height .3s ease,background-color .3s ease}.header--shrink{height:5rem;background-color:#05080c}.header__wrapper{display:flex;align-items:center;justify-content:space-between;height:100%;padding:0 2rem}.header__logo{display:flex;align-items:center;justify-content:unset;font-size:2.5rem;font-weight:600}.header__nav-list{display:flex;align-items:center;justify-content:unset;grid-gap:10px;gap:10px}.header__nav-item{padding:5px 0;font-weight:700;position:relative;font-size:1.5rem}.header__nav-item::after{content:"";position:absolute;bottom:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);width:0;transition:width .5s ease;height:2px;background-color:red}.header__nav-item--active::after,.header__nav-item:hover::after{width:100%}
*{box-sizing:border-box;margin:0;padding:0;-webkit-tap-highlight-color:transparent}body{font-family:"Dosis",sans-serif;margin:0;color:#fefffe;background-color:#05080c}a{text-decoration:none;color:unset}@media(hover: hover){a:hover{color:red}}button,input{outline:none}ul{list-style-type:none}img{max-width:100%}.container{max-width:1660px;margin:0 auto}.section{padding:0 2rem}.section__header{display:flex;align-items:center;justify-content:space-between}.wrapper{display:flex;justify-content:space-between}@media screen and (max-width: 767px){.wrapper{flex-direction:column}}
